import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoadingController, ModalController, ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.page.html',
  styleUrls: ['./contact.page.scss'],
})
export class ContactPage implements OnInit {
  menuItems = [
    {
      icon: 'home',
      name: 'Home',
      routerLink: './home',
    },
  ];
  user;
  role;
  email = {
    name: null,
    email: null,
    phone: null,
    message: null,
  };
  loader;
  constructor(
    private api: ApiService,
    private common: CommonService,
    public modalController: ModalController,
    public loadingController: LoadingController,
    private router: Router,
    private toastController: ToastController
  ) {}

  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    this.transformMenu();
  }

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    if (this.menuItems.length <= 1) {
      this.role = JSON.parse(localStorage.getItem('role'));
      this.user = this.common.getUser();
      this.transformMenu();
    }
  }

  ionViewWillLeave() {
    this.menuItems = [
      {
        icon: 'home',
        name: 'Home',
        routerLink: './home',
      },
    ];
    console.log('reset nav');
  }

  logout() {
    localStorage.removeItem('user');
    this.router.navigate(['./home']);
    window.location.reload();
  }
  transformMenu() {
    if (
      JSON.parse(localStorage.getItem('user')) === undefined ||
      JSON.parse(localStorage.getItem('user')) === null
    ) {
      console.log('Guest');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      (this.user.role.id === 'a' || this.user.role.id === 'l')
    ) {
      console.log('admin and landlord con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'people',
          name: 'Tenants',
          routerLink: './tenants',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      this.user.role.id === 't'
    ) {
      console.log('tenant con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: './queries',
        }
      );
    } else {
      console.log('team');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    }
  }
  checkRoute(route) {
    if (route.name === 'Logout') {
      localStorage.clear();
      this.router.navigate(['./home']);
    } else {
      this.router.navigate([route.routerLink]);
    }
  }

  async presentLoading() {
    console.log('loader');

    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        res.present();
      });
  }

  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  sendEmail(form) {
    if (form.invalid) {
      this.presentToast('Please fill in mandatory fields!', 'failed');
      return false;
    } else {
      this.presentLoading();
      this.api.sendContactEmail('property/sendContactEmail', form.value).subscribe((res) => {
        if (!res?.err) {
          this.loader.dismiss();
          this.presentToast('Email Sent Successfully', 'success');
          setTimeout(() => {
            window.location.reload();
          }, 2200);
        } else {
          this.loader.dismiss();
          this.presentToast(res?.err, 'failed');
        }
      });
    }
  }
}
